/// <reference types="react-scripts" />

import React from "react";

export enum RKey {
  Info = "Info",
  AlbumModal = "AlbumModal",
  Window = "Window",
}

declare global {
  type px = string;

  interface SizeComponentProps {
    gw: (width: number) => px;
    gh: (width: number) => px;
  }

  type Styled<T> = React.FC<T & SizeComponentProps>;

  interface Info {
    groom: {
      kakaoPay: string;
      account: string;
      father: {
        phone: string;
        kakao: string;
      };
      mother: {
        phone: string;
        kakao: string;
      };
    };
    bride: {
      kakaoPay: string;
      account: string;
      father: {
        phone: string;
        kakao: string;
      };
      mother: {
        phone: string;
        kakao: string;
      };
    };
    album_main: string;
    album: string[];
  }
}
