import { LikeButton } from "./LikeButton";
import { modalState, MoreButton } from "./Album/MoreButton";
import {
  ButtonWrap,
  GalleryArea,
  MainPhoto,
  MainPhotoWrap,
  Page,
  Wording,
  WordingArea,
} from "./styeld";
import { useRecoilState, useRecoilValue } from "recoil";
import { infoJson } from "@/recoil";
import { ClearButton } from "@/atoms";

export const PageGallery: React.FC = () => {
  const info = useRecoilValue(infoJson);
  const [, setIsOpen] = useRecoilState<boolean>(modalState);
  const openModal = () => setIsOpen(true);

  return (
    <Page>
      <GalleryArea>
        <MainPhotoWrap>
          <ClearButton onClick={openModal}>
            <MainPhoto src={info.album_main} />
          </ClearButton>
        </MainPhotoWrap>
        <ButtonWrap>
          <LikeButton />
           {0}
          <MoreButton />
        </ButtonWrap>
      </GalleryArea>
      <WordingArea>
        <Wording />
      </WordingArea>
    </Page>
  );
};
