import { windowStatus } from "@/recoil";
import { useEffect, useRef } from "react";
import Slider from "react-slick";
import { useRecoilState } from "recoil";

import { PageCalendar } from "./Calendar";
import { PageGallery } from "./Gallery";
import { PageInfo } from "./Info";
import { PageMain } from "./Main";
import { PageMap } from "./Map";

const settings = {
  speed: 250,
  slidesToShow: 1,
  slidesToScroll: 1,
  infinite: false,
  arrows: false,
  dots: false,
  easing: "ease-in-out",
};

export const Layout: React.FC = () => {
  const ref = useRef<HTMLDivElement>(null);
  const [, setWindow] = useRecoilState(windowStatus);

  useEffect(() => {
    const set = () =>
      setWindow({
        width: ref.current?.clientWidth ?? 1,
        height: ref.current?.clientHeight ?? 1,
      });

    set();
    window
      .matchMedia("only screen and (min-width: 510px)")
      .addEventListener("change", set);
  }, [setWindow]);

  return (
    <div ref={ref} id="inner">
      <Slider {...settings}>
        <PageMain />
        <PageCalendar />
        <PageGallery />
        <PageMap />
        <PageInfo />
      </Slider>
    </div>
  );
};
