import { useState } from "react";

import { ClearButton, Component, useGwGh } from "@/atoms";

import { ReactComponent as CONTACT } from "../svg/contact.svg";
import { ContactModal } from "./ContactModal";

export const ContactButton: React.FC = (arg) => {
  const [gh] = useGwGh();
  const [isOpen, setIsOpen] = useState(false);
  const openModal = () => setIsOpen(true);

  return (
    <>
      <ClearButton style={{ marginTop: gh(157) }} onClick={openModal}>
        <Component Base={CONTACT} />
      </ClearButton>
      <ContactModal isOpen={isOpen} setIsOpen={setIsOpen} />
    </>
  );
};
